.fade-out-enter.fade-out-enter-active {
  transform: translate(0, -103px);
  transition: all 500ms ease-out;
  opacity: 0;
  z-index: -1;
}

.fade-out-exit.fade-out-exit-active {
  transform: translate(0, 0px);
  opacity: 0;
  z-index: -1;
}

.fade-out-exit-done {
  transform: translate(0, -0);
  opacity: 0;
  z-index: 0;
}
