.liftItem-enter-done {
  transform: translate(0, 0px);
  opacity: 1;
  z-index: 1;
}

.liftItem-exit.liftItem-exit-active {
  transform: translate(0, -103px);
  transition: transform 500ms ease-in 500ms;
  opacity: 1;
  z-index: 1;
}

.liftItem-exit-done {
  transform: translate(0, -103px);
  opacity: 1;
  z-index: 1;
}

.cart-item-container {
  width: 100%;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
